<template>
  <div class="gap-10 container xl:max-w-screen-lg mx-auto p-6 space-y-10">
    <main class="w-full">
      <article class="max-w-full lg:max-w-full prose prose-code:!p-0 prose-code:font-fira w-full hover:prose-a:underline prose-a:no-underline prose-p:!my-2" v-html="content" data-content="content"></article>
    </main>
    <aside class="w-full">
      <div class="space-y-4 top-6 sticky">
        <header class="w-full border-b border-gray-100">
          <div class="table border-b border-teal-600 text-teal-600 pb-3">
            <p class="font-medium rtl:font-semibold text-sm">Settings</p>
          </div>
        </header>
        <div>
          <VueTailwindTags
            :isOut="options.isOut"
            :limit="options.limit"
            :showLimit="options.showLimit"
            :clearLastItem="options.clearLastItem"
            :showRemoveAll="options.showRemoveAll"
            :placeholder="options.placeholder"
            :showAddTag="options.showAddTag"
            :styled="options.styled"
            :limitText="options.limitText"
            v-model="tags"
          />
        </div>
        <div class="prose lg:max-w-full prose-code:!p-0">
          <pre class="text-sm prose-pre:whitespace-normal">{{ options }}  {{ tags.map((e, i) => `'${i}': '${e}'`) }}</pre>
        </div>
        <form class="w-full space-y-4">
          <div class="form-group">
            <div class="with-label">
              <input type="text" name="placeholder" id="placeholder" placeholder="..." v-model="options.placeholder" />
              <label for="placeholder">Placeholder</label>
            </div>
          </div>
          <div class="form-group" v-if="!options.showLimit">
            <div class="with-label">
              <input type="text" name="Limit" min="1" id="Limit" placeholder="..." v-model.number="options.limit" />
              <label for="limit">Limit</label>
            </div>
          </div>
          <div class="actions-group !gap-4 justify-between">
            <div class="form-group-with-check" v-for="(item, index) in buttons" :key="index">
              <input type="checkbox" :name="item" :true-value="true" :false-value="false" :id="item" v-model="options[item]" />
              <label :for="item" class="capitalize">{{ item.replace(/[A-Z]/g, (e) => ` ${e}`) }}</label>
            </div>
          </div>
          <div class="form-group" v-if="options.showLimit">
            <div class="with-label">
              <input type="number" name="limit" min="1" id="limit" :placeholder="limit" v-model.number="options.limit" class="flex-1" />
              <label for="color">Limit</label>
            </div>
          </div>
          <div class="actions-group !gap-4">
            <div class="form-group flex-[0_0_100%] sm:flex-[1_1_40%]" v-for="(item, index) in Object.keys(options.styled).map((e) => e.replace(/(-|_)/g, ' '))" :key="index">
              <div class="with-label">
                <input type="text" :name="item" :id="item" :placeholder="item" v-model="options.styled[item]" class="flex-1" />
                <label :for="item" class="capitalize">{{ item }}</label>
              </div>
            </div>
          </div>
          <p class="text-gray-400 font-medium rtl:font-semibold text-sm italic mt-2">
            To use <span class="text-teal-600 bg-gray-100 inline-block rounded-sm p-0.5 px-1">LimitText</span> or <span class="text-teal-600 bg-gray-100 inline-block rounded-sm p-0.5 px-1">customValidation</span> install the component first
            <a href="https://npmjs.com/package/vue-tailwind-tags" class="text-sky-600 inline-block rounded-sm underline not-italic">Install now</a>
          </p>
        </form>
      </div>
    </aside>
  </div>
</template>

<script>
  import VueTailwindTags from 'vue-tailwind-tags';
  import 'vue-tailwind-tags/dist/vue-tailwind-tags.css';
  export default {
    name: 'HomePage',
    data() {
      return {
        options: {
          isOut: false,
          limit: 0,
          showLimit: false,
          clearLastItem: true,
          showRemoveAll: true,
          placeholder: 'Type tag here...',
          showAddTag: true,
          limitText: (e) => `+${e}`,
          styled: {
            parent: null,
            input: null,
            tag: null,
            button: null,
            remove_all: null,
          },
        },
        content: null,
        tags: [],
        buttons: ['isOut', 'showLimit', 'clearLastItem', 'showRemoveAll', 'showAddTag'],
      };
    },
    created() {
      this.content = require(`!!html-loader!markdown-loader!@/assets/articles/installation.md`).default;
    },
    computed: {
      style() {
        return `
            .custom {
              width: ${this.custom_width}px;
              height: ${this.custom_height}px;
            }
        `;
      },
    },
    methods: {},
    components: { VueTailwindTags },
  };
</script>

<style lang="scss">
  @import 'highlight.js/scss/night-owl.scss';
  [data-anchor]:not([data-anchor='']) {
    &:hover {
      .anchor {
        @apply relative no-underline hover:no-underline;
        &:before {
          content: '#';
          @apply relative;
        }
      }
    }
  }
</style>
